import styled from 'styled-components'
import { Query } from 'react-apollo'
import gql from 'graphql-tag'

import Todo from './Todo'

const TodosList = styled.div``
// Query all Todos from prisma, and map Todo components

// use perpage, first, skip for pagination
const perPage = 100

const ALL_TODOS_QUERY = gql`
  query ALL_TODOS_QUERY($task: String, $id: ID, $skip: Int = 0, $first: Int = ${perPage}) {
    todoes(orderBy: createdAt_DESC, skip: 0) {
      id
      task
    }
  }
`

// query todos, map to display
class Todos extends React.Component {
  render() {
    return (
      <div>
        <Query query={ALL_TODOS_QUERY}>
          {({ data, loading, error }) => {
            if (loading) return <p>Loading...</p>
            if (error) return <p>Error: {error.message}</p>
            console.log(data)
            return null
          }}
        </Query>
      </div>
    )
  }
}

export default Todos
